import React, { useState } from "react";
import { Button, Col, Form, FormGroup, Input, Label, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { imgURL } from "../../redux/config";
import AvatarImage from "../../assets/images2/avatar.png";
import { useForm } from "react-hook-form";
import { updatePassword } from "../../redux/actions";

const MyProfile = () => {
  const adminInfo = useSelector((state) => state.auth.authData);
  const { register, handleSubmit, errors } = useForm();

  const dispatch = useDispatch();

  const initinalValues = {
    email: adminInfo.email,
    currentPassword: "",
    newPassword: "",
  };

  const [formValues, setFormValues] = useState(initinalValues);

  const handleUpdatePassword = () => {
    console.log(formValues);
    dispatch(updatePassword(formValues));
  };

  const adminProfileDetails = (data) => (
    <>
      <div className={"m-5"}>
        <h3 className={"font-weight-bold"}>Admin Details</h3>
      </div>
      <div className="row">
        <div className="col-md-4">
          <div className="profile-img">
            {/* <div>
              <img
                src={data.image ? imgURL + data.image : AvatarImage}
                style={{ width: 100, height: 100 }}
              />
            </div> */}
          </div>
        </div>
        <div className="col-md-6">
          <div className="profile-head">
            <h5 className={"font-weight-bold"}>
              {/*{selectedDetails.profile.name}*/}
            </h5>
            {/*<h6>email</h6>*/}
            <ul className="nav nav-tabs" id="myTab" role="tablist">
              <li className="nav-item">
                <a
                  className="nav-link active"
                  id="home-tab"
                  data-toggle="tab"
                  href="#"
                  role="tab"
                  aria-controls="home"
                  aria-selected="true"
                >
                  About
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-md-8">
          <div className="profile-tab" id="myTabContent">
            <div
              className="tab-pane fade show active merchant-details"
              id="home"
              role="tabpanel"
              aria-labelledby="home-tab"
            >
              <Row>
                <Col>
                  <Form onSubmit={handleSubmit(handleUpdatePassword)}>
                    <FormGroup>
                      <Label for="email">Email</Label>
                      <Input
                        type="email"
                        name="email"
                        id="email"
                        defaultValue={data.email}
                        disabled={true}
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="currentPassword">Current Password</Label>
                      <Input
                        type="password"
                        name="currentPassword"
                        id="currentPassword"
                        placeholder={"Enter your current password"}
                        onChange={(e) =>
                          setFormValues({
                            ...formValues,
                            currentPassword: e.target.value,
                          })
                        }
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="newPassword">New Password</Label>
                      <Input
                        type="password"
                        name="newPassword"
                        id="newPassword"
                        placeholder={"Enter your new password"}
                        onChange={(e) =>
                          setFormValues({
                            ...formValues,
                            newPassword: e.target.value,
                          })
                        }
                      />
                    </FormGroup>
                    {/*<div className={"w-100 text-right"}>*/}
                    <Button type={"submit"} color={"primary"}>
                      Update Password
                    </Button>
                    {/*</div>*/}
                  </Form>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </div>
    </>
  );

  return (
    <>
      <div>{adminProfileDetails(adminInfo)}</div>
    </>
  );
};

export default MyProfile;
